import React, {Component} from 'react'
import {connect} from 'react-redux'
import PropTypes from 'prop-types'

import RouterH from 'routers/RouterH.js'

import {MuiThemeProvider, createMuiTheme} from '@material-ui/core/styles'
import appTheme from 'utils/const/theme.js'

import Audio from 'components/Audio/Audio.js'

import {throttle} from "utils/func/util.js"

import {autoLogin} from 'store/actions/auth.js'
import {resizeScreen, checkLang, setInstallPrompt} from 'store/actions/appSettings.js'

import 'assets/styles/index.scss'

const theme = createMuiTheme(appTheme);

export class App extends Component {
    componentDidMount() {
        // TODO comment outologin
        // if (this.props.autoLoginAction) this.props.autoLoginAction();

        if (this.props.checkLangAction) this.props.checkLangAction();

        // проверка размеров окна при старте приложения
        if (this.props.resizeScreenAction) this.props.resizeScreenAction();

        // слежение за ресайзом окна
        const throttledResize = throttle(() => {
            this.props.resizeScreenAction();
        }, 300);

        window.onresize = () => {
            throttledResize();
        };

        // propmt
        window.addEventListener(`beforeinstallprompt`, (e) => {
            e.preventDefault();
            this.props.setInstallPromptAction(e);
            // eslint-disable-next-line no-console
            console.log(`getPromtSW`, e)
        });
    }

    render() {
        return (
            <MuiThemeProvider theme={theme}>
                <RouterH/>
                <Audio />
            </MuiThemeProvider>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        isMobile: state.appSettings.isMobile,
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        autoLoginAction: () => dispatch(autoLogin()),
        checkLangAction: () => dispatch(checkLang()),
        setInstallPromptAction: (installPrompt) => dispatch(setInstallPrompt(installPrompt)),
        resizeScreenAction: (widthScreen) => dispatch(resizeScreen(widthScreen))
    }
}

App.propTypes = {
    isMobile: PropTypes.bool,
    autoLoginAction: PropTypes.func,
    checkLangAction: PropTypes.func,
    setInstallPromptAction: PropTypes.func,
    resizeScreenAction: PropTypes.func,
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
