import React from "react"
import PropTypes from "prop-types"

import ButtonBase from "@material-ui/core/ButtonBase"

import classes from "./Btn1.module.scss"

class Btn1 extends React.PureComponent {
    render() {
        const cls = [classes.btn];
        if (this.props.color) cls.push(classes[this.props.color]);

        return (
            <div className={classes.main}>
                <ButtonBase
                    focusRipple
                    className={cls.join(` `)}
                    onClick={this.props.onClick}
                >
                </ButtonBase>
            </div>
        );
    }
}


Btn1.propTypes = {
    color: PropTypes.string,
    onClick: PropTypes.func,
};

export default Btn1;
