import React from "react"
import PropTypes from "prop-types"

import classes from "./LCDSymbol.module.scss"

class LCDSymbol extends React.Component {
    render() {
        return (
            <div className={classes.symbol}>
                {
                    this.props.LCDSymbol.map((it,i) =>
                        <div
                            key={i}
                            className={classes.symbolColumn}
                        >
                            {
                                it.map((item,j) =>
                                    <div
                                        key={j}
                                        className={item === `1` ? classes.squareActive : classes.square}
                                    >
                                    </div>
                                )
                            }
                        </div>
                    )
                }
            </div>

        );
    }
}

LCDSymbol.propTypes = {
    LCDSymbol: PropTypes.array,
};

export default LCDSymbol;
