import React from "react"
import PropTypes from "prop-types"

import LCDSymbolSVG from "components/EditorComponents/LCDSymbolSVG/LCDSymbolSVG.js"

import classes from "./LCDLine3.module.scss"

const line3 = `АКТ.: - - - - - - - -         `;

class LCDLine3 extends React.PureComponent {
    render() {
        const textLine = this.props.textLine || line3;
        return (
            <div className={classes.textRow}>
                {
                    /* 24 characters */
                    textLine.slice(0, 24).split(``).map((it,i) =>
                        <div
                            key={`${it}_${i}`}
                            className={classes.textRowItem}
                        >
                            <LCDSymbolSVG
                                LCDSymbol={it}
                            />
                        </div>
                    )
                }
            </div>
        )
    }
}


LCDLine3.propTypes = {
    textLine: PropTypes.array.isRequired,
};

export default LCDLine3;
