import React from "react"
import PropTypes from "prop-types"

import Line from "./Line/Line.js"

import classes from "./Lines.module.scss"

class Lines extends React.Component {
    onBreakPointClick = (idLine, valueActive) => {
        const lines = this.props.lines;
        lines[idLine].breakPoint.active = valueActive;

        this.props.onChange(this.props.lines.id, lines)
    }
    render() {
        return (
            <svg
                height="1240"
                width="1151"
                xmlns="http://www.w3.org/2000/svg"
                xmlnsXlink="http://www.w3.org/1999/xlink"
            >
                <defs>
                    {/* marker for line start */}
                    <marker
                        id="startarrow"
                        markerWidth="10"
                        markerHeight="7"
                        refX="0" refY="3.5"
                        orient="auto"
                    >
                        <polygon points="10 0, 7 3.5, 10 7, 0 3.5" fill="black" />
                    </marker>

                    {/* marker for line end */}
                    <marker
                        id="endarrow"
                        markerWidth="10"
                        markerHeight="7"
                        refX="10" refY="3.5"
                        orient="auto"
                    >
                        <polygon points="0 0, 10 3.5, 0 7, 3 3.5" fill="black" />
                    </marker>


                    {/* symbol breakPoint*/}
                    <symbol id="x" >
                        <line
                            x1={0} y1={0}
                            x2={20}  y2={20}
                            stroke="#ff0000"
                            strokeWidth={`4`}
                        />
                        <line
                            x1={20} y1={0}
                            x2={0}  y2={20}
                            stroke="#ff0000"
                            strokeWidth={`4`}
                        />
                    </symbol>

                    {/* symbol breakPoint*/}
                    <symbol id="breakPoint" className={classes.breakPoint}>
                        <rect
                            className={classes.rect}
                            width="40" height="6"
                            x="-10" y="16"
                            transform="rotate(-45 10 10)"
                        />
                        <rect
                            className={classes.rect}
                            width="40" height="6"
                            x="0" y="6"
                            transform="rotate(45 10 10)"
                        />
                    </symbol>


                </defs>
                {
                    this.props.lines.map((it,i) => <Line key={i} item={it} onBreakPointClick={this.onBreakPointClick}/>)
                }
            </svg>
        );
    }
}


Lines.propTypes = {
    lines: PropTypes.array,
    onChange: PropTypes.func,
};

export default Lines;
