import React from "react"
import PropTypes from "prop-types"

import ButtonBase from "@material-ui/core/ButtonBase"
import Typography from "@material-ui/core/Typography"

import classes from "./Signal.module.scss"

/**
{
type: `signal`,
top: 410,
left: 10,
text: `Неисправность`,
width: `150px`,
active: false,
activeColor: `orange`, `green`, `red`
disabled: true,
},
 */
class Signal extends React.Component {
    onSignalClick = () => {
        const item = this.props.item;
        item.active = !this.props.item.active;

        this.props.onChange(this.props.item.id, item)
    }
    render() {
        const {item} = this.props;

        const lineStyle = {
            top: item.top || ``,
            left: item.left || ``,
            width: item.width || `100px`,
        }

        const cls = [classes.btn];
        if (item.active && item.activeColor) cls.push(classes[item.activeColor]);

        return (
            <div
                className={classes.main}
                style={lineStyle}
            >
                <ButtonBase
                    focusRipple
                    className={cls.join(` `)}
                    disabled={item.disabled}
                    onClick={this.onSignalClick}
                >
                    <Typography
                        variant="button"
                        color="inherit"
                    >
                        {item.text}
                    </Typography>
                </ButtonBase>
            </div>
        );
    }
}


Signal.propTypes = {
    item: PropTypes.object,
    onChange: PropTypes.func.isRequired,
};

export default Signal;
