import React from "react"
import {connect} from "react-redux"
import PropTypes from "prop-types"

import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import FormLabel from '@material-ui/core/FormLabel'
import Switch from '@material-ui/core/Switch'
import Slider from '@material-ui/lab/Slider'

import {setVolume} from "store/actions/sound.js"

import Paper from "@material-ui/core/Paper"

import classes from "./PanelSettings.module.scss"

class PanelSettings extends React.Component {
    constructor(props){
        super(props);

        this.state = {
            volumeValue: this.props.volumeValue
        }
    }
    onAlgoritmClick = (event, newAlgoritm) => {
        this.props.onSettingsChange(`algoritm`, newAlgoritm)
    };
    onVolumeValueChange = (event, volumeValue) => { // сохранение значения volumeValue для отрисовки на слайдере
        this.setState({volumeValue});
    };
    onVolumeValueSet = (event, volumeValue) => { // передача значения volumeValue стору
        this.props.setVolume(volumeValue);
    };
    onIncreaseDisplayChange = (event, volumeValue) => {
        this.props.onSettingsChange(`increaseDisplay`, volumeValue);
    };

    render() {
        return (
            <div className={classes.main}>
                <Paper className={classes.PaperBlock}>
                    <FormControl component="fieldset">
                        <FormLabel component="legend" className={classes.formLabel}>
                            Увеличить дисплей
                        </FormLabel>
                        <div className={classes.switch}>
                            <Switch
                                checked={this.props.settings.increaseDisplay}
                                onChange={this.onIncreaseDisplayChange}
                                value="checkedB"
                                color="primary"
                            />
                        </div>
                    </FormControl>
                </Paper>

                <Paper className={classes.PaperBlock}>
                    <FormControl component="fieldset">
                        <FormLabel component="legend" className={classes.formLabel}>
                            Выбор алгоритма работы
                        </FormLabel>
                        <RadioGroup
                            aria-label="position"
                            name="position"
                            value={this.props.settings.algoritm}
                            onChange={this.onAlgoritmClick}
                            row
                        >
                            <FormControlLabel
                                value="1"
                                control={<Radio color="primary" />}
                                label="1"
                                labelPlacement="top"
                            />
                            <FormControlLabel
                                value="2"
                                control={<Radio color="primary" />}
                                label="2"
                                labelPlacement="top"
                            />
                            <FormControlLabel
                                value="3"
                                control={<Radio color="primary" />}
                                label="3"
                                labelPlacement="top"
                            />
                        </RadioGroup>
                    </FormControl>
                </Paper>

                <Paper className={classes.PaperBlock}>
                    <FormControl component="fieldset">
                        <FormLabel component="legend" className={classes.formLabel}>
                            Управление звуком
                        </FormLabel>
                        <div className={classes.sliderContainer}>
                            <Slider
                                value={this.state.volumeValue}
                                aria-labelledby="label"
                                onChange={this.onVolumeValueChange}
                                onDragEnd={this.onVolumeValueSet}
                            />
                        </div>
                    </FormControl>
                </Paper>
            </div>
        );
    }
}


const mapStateToProps = state => {
    return {
        volumeValue: state.sound.volumeValue,
    };
};
const mapDispatchToProps = (dispatch) => {
    return {
        setVolume: (volumeValue) => dispatch(setVolume(volumeValue)),
    }
}

PanelSettings.propTypes = {
    settings: PropTypes.object,
    volumeValue: PropTypes.number,
    onSettingsChange: PropTypes.func,
    setVolume: PropTypes.func,
};

export default connect(mapStateToProps, mapDispatchToProps)(PanelSettings);
