import React from 'react'
import { connect } from "react-redux"
import PropTypes from 'prop-types'

import Header from 'layout/Header/Header.js'
import AsideMenu from 'layout/AsideMenu/AsideMenu.js'

import classes from './LayoutPage.module.scss'


class LayoutPage extends React.Component {
    render() {
        const cls = [classes.bodyPage];
        if (this.props.isDesktop) cls.push(classes.isDesktop);

        return (
            <div className={classes.main}>
                <Header />
                <AsideMenu />
                <main className={cls.join(` `)}>
                    {this.props.children}
                </main>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        showAsideMenu: state.appSettings.showAsideMenu,
        isDesktop: state.appSettings.isDesktop,
    };
};

LayoutPage.propTypes = {
    children: PropTypes.oneOfType([
        PropTypes.arrayOf(PropTypes.node),
        PropTypes.node
    ]),
    isDesktop: PropTypes.bool.isRequired,
}

export default connect(mapStateToProps)(LayoutPage);
