import React from "react"
import { connect } from "react-redux"
import PropTypes from "prop-types"

import Editor from "components/Editor/Editor.js"

import META_17820 from "utils/const/scheme/meta17820.js"
import MESSAGE from "utils/const/message.js"

import {setTrack} from "store/actions/sound.js"

class SchemeMeta17820 extends React.Component {
    constructor(props){
        super(props);

        this.lastTimerID = null;

        this.state = {
            scheme: JSON.parse(JSON.stringify(META_17820)), // deep copy obj
        }
    }

    componentDidUpdate(prevProps) {
        // при появлении новых настроек сверху пересматр схемы с новыми настройками
        if (prevProps.settings !== this.props.settings) {
            const scheme = {...this.state.scheme};
            this.getCheckScheme(scheme, this.props.settings)
        }
    }
    componentWillUnmount(){
        clearTimeout(this.lastTimerID);

        // остановить трек звук
        this.props.setTrack(``, false);
    }

    setSounds2ForFire() {
        const scheme = {...this.state.scheme};
        scheme.settings.setSounds2ForFire = true;
        this.getCheckScheme(scheme)
    }
    getCheckScheme(scheme, settings) {
        const {elements, message} = scheme;
        let track = ``; // нет звука,
        let loopNewSound = true; // нет звука,

        // пришли новые настроки схемы от родителя
        if (settings) {
            scheme.settings = settings
        }

        const meta17820 = {
            activeZone: [false, false, false, false, false, false, false, false],
            fireZone: [false, false, false, false, false, false, false, false],
            blocking: false, // блокирвка с пульта META18580
            fire: false, // пожар
            goI4S: false, // ГО и ЧС
            problem: false, // неисправность
            power: false, // питание
            offZummer: false, // откл зумерра
            offVolume: false, // откл звука
            signal: false, // сигнал
        }
        const elID = {
            meta18580: null
        }

        // перебираем все элементы и получаем новое состояние прибора
        elements.forEach(it => {
            switch (it.type) {
            case `lines`: // check lines breakPoint
                it.lines.forEach(item => {
                    if (item.breakPoint && item.breakPoint.active) {
                        meta17820.problem = true;
                    }
                    if (item.breakPoint && item.powerLine) {
                        meta17820.power = !item.breakPoint.active;
                    }
                })
                break
            case `signal`: // checkFire
                if (it.text[0] === `П` && it.active) { // сигнал пожара
                    meta17820.fireZone[it.zoneID] = true;
                    meta17820.fire = true;
                }
                else if (it.text[0] === `Г` && it.active) { // сигнал ГО и ЧС
                    meta17820.goI4S = true;
                }
                break
            case `meta18580`: // check
                elID.meta18580 = it.id
                if (it.keyOn) { // вкл блокировка на пульте
                    meta17820.blocking = true;
                }
                break
            }
        });



        // check power

        // check pult
        if (meta17820.blocking) {
            message.soundZones.text = MESSAGE.blocking;
            const meta18580 = elements[elID.meta18580];

            meta18580.zoneControls.forEach((it,i) => {
                // зона активна если активна кнопка зоны или общая кнопка
                meta17820.activeZone[i] = it.on || meta18580.controls[1].on
            })

            // если есть аквтиная зона и включена сирена
            if (meta18580.controls[0].on && meta17820.activeZone.includes(true)){ // включена сирена
                track = `sirena`;
            }
        }

        // check Fire в зависимости от выбора алгоритма
        else if (meta17820.fire) {
            switch (scheme.settings.algoritm) {
            case `1`:
                if (!scheme.settings.setSounds2ForFire) { // вывод первого сообщения в 1 зоны
                    meta17820.activeZone = [true, false, false, false, false, false, false, false];
                    track = `attention`;
                    message.soundZones.text = MESSAGE.fire1Sound1Signal;
                    loopNewSound = false;

                    // через 30 сек запуск лругого сообщения
                    this.lastTimerID = setTimeout(() => {
                        this.setSounds2ForFire();
                    }, 10000) // TODO изменить на значение 30 сек
                } else { // вывод второго сообщения в все зоны
                    meta17820.activeZone = meta17820.fireZone;
                    meta17820.activeZone[0] = true
                    track = `fire`;
                    message.soundZones.text = MESSAGE.fire1Sound2Signal;
                }

                break
            case `2`:
                // TODO вывод звука только в зоны пожара
                meta17820.activeZone = meta17820.fireZone;
                track = `fire`;
                message.soundZones.text = MESSAGE.fire2SoundSignal;
                break
            case `3`:
                meta17820.activeZone = [true, true, true, true, true, true, true, true];
                track = `fire`;
                message.soundZones.text = MESSAGE.fire3SoundSignal;
                break
            }
        }

        // check сигнал ГО и ЧС проходил если нет пожара и неисправности
        else if (meta17820.goI4S &&
            !meta17820.problem
        ) {
            message.soundZones.text = MESSAGE.goI4S;
            meta17820.activeZone = [true, true, true, true, true, true, true, true];
            track = `attention`;
        }

        // нет никаких активных режимов: пожар, неисправность, ГО
        else {
            scheme.message.soundZones.text = ``;
            scheme.settings.setSounds2ForFire = false;
        }

        // установка нового звука, если звук совпадает со старым то продолжит играть старый трек
        this.props.setTrack(track, loopNewSound);


        // check ActiveZone
        // показать активные зоны ( speaker)
        elements.map(it => {
            switch (it.type) {
            case `speaker`:
                it.soundActive = meta17820.activeZone[it.zoneID];
                break
            }

            return it
        });


        // new view
        // 22 - meta17820
        // 23 - meta18580
        elements[10].active = meta17820.problem; // signal  `Неисправность`
        elements[22].LEDs[1].on = meta17820.problem;
        elements[23].LEDs[0].on = meta17820.problem;

        elements[22].LEDs[0].on = meta17820.fire;
        elements[22].LEDs[2].on = meta17820.power;

        scheme.elements = elements;
        scheme.meta17820 = meta17820;

        this.setState({scheme});
    }

    // обработка изменения их схемы
    onEditorChange = (id, value) => {
        // id = index in scheme.elements
        const scheme = {...this.state.scheme};
        if (value) scheme.elements[id] = value;

        // проверка всей схемы с новыми условиями
        this.getCheckScheme(scheme)
    };
    render() {
        const {scheme} = this.state;
        return (
            <Editor
                scheme={scheme}
                onEditorChange={this.onEditorChange}
            />
        );
    }
}

const mapStateToProps = state => {
    return {
        register: state.steps.register,
    };
};
const mapDispatchToProps = (dispatch) => {
    return {
        setTrack: (track, loop) => dispatch(setTrack(track, loop)),
    }
}

SchemeMeta17820.propTypes = {
    settings: PropTypes.object,
    register: PropTypes.bool,
    setTrack: PropTypes.func,
};

export default connect(mapStateToProps, mapDispatchToProps)(SchemeMeta17820);
