import React from "react"
import PropTypes from "prop-types"

import classes from "./LED.module.scss"

class LED extends React.PureComponent {
    render() {
        const cls = [classes.led, classes[this.props.LEDColor]];
        if (this.props.on) cls.push(classes.on);

        return (
            <div
                className={classes.main}
            >
                {/* LED */}
                <div className={cls.join(` `)}></div>

                {/* LED text */}
                <p className={classes.text}>
                    {this.props.text}
                </p>

            </div>

        );
    }
}


LED.propTypes = {
    LEDColor: PropTypes.string,
    text: PropTypes.string,
    on: PropTypes.bool,
};

export default LED;
