import React from "react"
import { connect } from "react-redux"
import history from 'routers/history.js'
import PropTypes from "prop-types"

import LayoutPage from "layout/LayoutPage/LayoutPage.js"

import ButtonBase from "@material-ui/core/ButtonBase"
import Paper from "@material-ui/core/Paper"
import Typography from "@material-ui/core/Typography"

import fireAlertImg from "assets/img/components/fire_alert.png"

import {ROUTER_LINK} from 'utils/const/routerLink.js'

import classes from "./Home.module.scss"

class Home extends React.Component {
    constructor(props) {
        super(props);

        const steps = [
            {
                title: `Системы речевого оповещения при пожаре`,
                textBody1: `алгоритм работы МЕТА 17820 / МЕТА 17821`,
                img: fireAlertImg,
                imgTitle: `оповещение при пожаре`,
                imgAlt: `оповещение при пожаре`,
                linkTo: ROUTER_LINK.meta17820,
            },
            {
                title: `Тест производительности системы`,
                textBody1: `для проверки возможности создания одностраничника`,
                img: ``,
                linkTo: ROUTER_LINK.performanceTest,
            },
            {
                title: `Редактор v2.0`,
                textBody1: `для создания схем`,
                img: ``,
                linkTo: ROUTER_LINK.editor,
            },
        ];

        this.state = {
            steps,
        };
    }
    onButtonBaseClick = (indexBtn) => {
        history.push(this.state.steps[indexBtn].linkTo);
    };

    render() {
        return (
            <LayoutPage>

                <div className={classes.stepBlock}>
                    {this.state.steps.map((it, i) => (
                        <ButtonBase
                            focusRipple
                            key={it.title}
                            className={classes.step_item}
                            onClick={() => this.onButtonBaseClick(i)}
                        >
                            {
                                it.img &&
                                    <div className={classes.item_img}>
                                        <img
                                            className={classes.img}
                                            src={it.img}
                                            title={it.imgTitle}
                                            alt={it.imgAlt}
                                        />
                                    </div>
                            }
                            <Paper className={classes.PaperBlock}>
                                <Typography variant="h2" gutterBottom>
                                    {it.title}
                                </Typography>
                                <Typography variant="body1">
                                    {it.textBody1}
                                </Typography>
                            </Paper>
                        </ButtonBase>
                    ))}
                </div>
            </LayoutPage>
        );
    }
}

const mapStateToProps = state => {
    return {
        register: state.steps.register,
    };
};

Home.propTypes = {
    register: PropTypes.bool,
};

export default connect(mapStateToProps)(Home);
