import {
    STEPS_SET_REGISTER,
    STEPS_SET_AUTHORIZATION,
    STEPS_SET_CHANGE_PASSWORD,
    STEPS_SET_SHOW_OTHER_PASSWORDS,
} from '../actions/actionTypes';

const initialState = {
    register: true,
    authorization: false,
    changePassword: false,
    showOtherPasswords: false,
}


export default (state = initialState, action) => {
    switch (action.type) {
    case STEPS_SET_REGISTER:
        return {
            ...state,
            register: action.register,
        }
    case STEPS_SET_AUTHORIZATION:
        return {
            ...state,
            authorization: action.authorization,
        }
    case STEPS_SET_CHANGE_PASSWORD:
        return {
            ...state,
            changePassword: action.changePassword,
        }
    case STEPS_SET_SHOW_OTHER_PASSWORDS:
        return {
            ...state,
            showOtherPasswords: action.showOtherPasswords,
        }
    default:
        return state
    }
}
