import React, {Component} from 'react'
import {Redirect} from 'react-router-dom'
import {connect} from 'react-redux'
import PropTypes from 'prop-types'

import {userLogout} from 'store/actions/auth.js'

import {ROUTER_LINK} from 'utils/const/routerLink.js'

class Logout extends Component {
    constructor(props) {
        super(props);

        this.state = {
            redirect: null,
        }
    }

    componentDidMount() {
        if (this.props.auth.token !== null) {
            this.props.logoutAction();
        } else if (this.state.redirect === null) {
            const redirect = <Redirect to={ROUTER_LINK.home} />;
            this.setState({ redirect });
        }
    }

    componentDidUpdate() {
        if (this.props.auth.token === null && this.state.redirect === null) {
            const redirect = <Redirect to={ROUTER_LINK.home} />;
            this.setState({ redirect });
        }
    }

    render() {
        return (
            <div>
                {this.state.redirect}
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        auth: state.auth
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        logoutAction: () => dispatch(userLogout()),
    }
};

Logout.propTypes = {
    auth: PropTypes.object,
    logoutAction: PropTypes.func,
}

export default connect(mapStateToProps, mapDispatchToProps)(Logout)
