/* eslint-disable no-console */
import React from "react"
import PropTypes from "prop-types"

import LED from "components/EditorComponents/LED/LED.js"
import Btn1 from "components/EditorComponents/Btn1/Btn1.js"

import classes from "./Btn1LED.module.scss"

/**
{
LEDColor: string,
btnColor: string,
text: string,
on: bool,
}
 */

class Btn1LED extends React.Component {
    onBtnClick = () => {
        const item = this.props.item;
        item.on = !this.props.item.on;

        this.props.onChange(this.props.item.id, item)
    }
    render() {
        return (
            <div className={classes.main}>
                <div className={classes.LED}>
                    <LED
                        LEDColor={this.props.item.LEDColor}
                        text={this.props.item.text}
                        on={this.props.item.on}
                    />
                </div>

                <Btn1
                    color={this.props.item.btnColor}
                    onClick={this.onBtnClick}
                />
            </div>
        );
    }
}


Btn1LED.propTypes = {
    item: PropTypes.object,
    onChange: PropTypes.func.isRequired,
};

export default Btn1LED;
