import { LEXICON } from 'utils/const/lexicon.js'



/**
 * «Тормозилка», которая возвращает обёртку,
 * передающую вызов cb не чаще, чем раз в ms миллисекунд.
 *
 * @param cb
 * @param ms
 * @return {wrapper}
 */
export const throttle = (cb, ms) => {
    let isThrottled = false;
    let savedArgs = null;
    let savedThis = null;

    function wrapper(...arg) {
        if (isThrottled) {
            savedArgs = arg;
            savedThis = this;
            return;
        }

        cb.apply(this, arg);

        isThrottled = true;

        setTimeout(() => {
            isThrottled = false;
            if (savedArgs) {
                wrapper.apply(savedThis, savedArgs);
                savedArgs = null;
                savedThis = null;
            }
        }, ms);
    }

    return wrapper;
}

/**
 * взять текст из словаря
 * @param String
 * @param String
 * @return String
 */
export const getText = (field, lang) => LEXICON[lang][field] || LEXICON.en[field] || ``;
