import React from "react"
import PropTypes from "prop-types"

/**
{
    type: `line`,
    x1:`160`,
    x2:`280`,
    y1:`25`,
    y2:`25`,
    strokeWidth: 5,
    markerStart: true,
    markerEnd: true
},
 */
class Line extends React.Component {
    onBreakPointClick = () => {
        this.props.onBreakPointClick(this.props.item.id, !this.props.item.breakPoint.active)
    }
    render() {
        const {item} = this.props;

        return (
            <svg onClick={this.onBreakPointClick}>
                <line
                    x1={item.x1} y1={item.y1}
                    x2={item.x2}  y2={item.y2}
                    stroke="#333333"
                    strokeWidth={item.strokeWidth || `2`}
                    markerStart={item.markerStart && `url(#startarrow)`}
                    markerEnd={item.markerEnd && `url(#endarrow)`}
                />
                {
                    item.breakPoint &&
                    <use xlinkHref="#breakPoint"
                        x={item.breakPoint.x} y={item.breakPoint.y}
                        fill={item.breakPoint.active ? `red` : `grey`}
                        fillOpacity={item.breakPoint.active ? `.6` : `.3`}
                    />
                }
            </svg>
        );
    }
}


Line.propTypes = {
    item: PropTypes.object,
    onBreakPointClick: PropTypes.func,
};

export default Line;
