import React from "react"
import {connect} from "react-redux"
import PropTypes from "prop-types"

import Typography from "@material-ui/core/Typography"

import LayoutPage from "layout/LayoutPage/LayoutPage.js"
import SchemeMeta17820 from "components/Scheme/SchemeMeta17820/SchemeMeta17820.js"

import META_17820 from "utils/const/scheme/meta17820.js"

import classes from "./Editor2.module.scss"

class Editor2 extends React.Component {
    constructor(props){
        super(props)

        this.state = {
            settings: {...META_17820.settings},
        }
    }

    onSettingsChange = (field, value) => {
        const settings = {...this.state.settings};
        settings[field] = value;

        this.setState({settings});
    };

    render() {
        const {settings} = this.state;
        return (
            <LayoutPage>
                <Typography variant="h1" gutterBottom>
                    Редактор
                </Typography>
                <Typography variant="body1" gutterBottom>
                    версия 2.0
                </Typography>

                <div className={classes.settings}>

                </div>

                <div className={classes.editor}>
                    <SchemeMeta17820 settings={settings}/>
                </div>
            </LayoutPage>
        );
    }
}

const mapStateToProps = state => {
    return {
        register: state.steps.register,
    };
};

Editor2.propTypes = {
    register: PropTypes.bool,
};

export default connect(mapStateToProps)(Editor2);
