import React from "react"
import PropTypes from "prop-types"

import Switch from "components/EditorComponents/Switch/Switch.js"

import classes from "./PanelControls.module.scss"

class PanelControls extends React.Component {
    render() {
        return (
            <div className={classes.main}>
                {this.props.controls.map((it,i) => <Switch key={i} {...it}/>)}
            </div>
        );
    }
}


PanelControls.propTypes = {
    controls: PropTypes.array.isRequired,
};

export default PanelControls;
