import {
    APP_SETTINGS_RESIZE_SCREEN,
    APP_SETTINGS_SWITCH_ASIDE_MENU,
    APP_SETTINGS_SET_LANG,
    APP_SETTINGS_SET_INSTALL_PROMPT,
    APP_SETTINGS_SET_LCD_SYMBOLS
} from '../actions/actionTypes'

const initialState = {
    isMobile: false,
    isTablet: false,
    isDesktop: true,
    showAsideMenu: false,
    lang: `ru`, // `ru`  `en'
    installPrompt: null, // propmpt for PWA
}

export default (state = initialState, action) => {
    switch (action.type) {
    case APP_SETTINGS_RESIZE_SCREEN:
        return {
            ...state,
            isMobile: action.isMobile,
            isTablet: action.isTablet,
            isDesktop: action.isDesktop,
            showAsideMenu: action.showAsideMenu
        }
    case APP_SETTINGS_SET_LANG:
        return {
            ...state,
            lang: action.lang,

        }
    case APP_SETTINGS_SWITCH_ASIDE_MENU:
        return {
            ...state,
            showAsideMenu: action.showAsideMenu
        }
    case APP_SETTINGS_SET_INSTALL_PROMPT:
        return {
            ...state,
            installPrompt: action.installPrompt
        }
    case APP_SETTINGS_SET_LCD_SYMBOLS:
        return {
            ...state,
            LCDSymbols: action.LCDSymbols
        }
    default:
        return state
    }
}
