import {
    APP_SETTINGS_RESIZE_SCREEN,
    APP_SETTINGS_SWITCH_ASIDE_MENU,
    APP_SETTINGS_SET_LANG,
    APP_SETTINGS_SET_INSTALL_PROMPT,
    APP_SETTINGS_SET_LCD_SYMBOLS
} from './actionTypes';

import {MAX_MOBILE_SCREEN, MAX_TABLET_SCREEN} from 'utils/const/appSettings';

export const resizeScreen = () =>
    dispatch => {
        const widthScreen = window.innerWidth //  узнать текущий размер окна
            || document.documentElement.clientWidth
            || document.body.clientWidth;

        const isMobile = (widthScreen <= MAX_MOBILE_SCREEN);
        const isTablet = (widthScreen <= MAX_TABLET_SCREEN && !isMobile);
        const isDesktop = !(isMobile || isTablet);
        const showAsideMenu = isDesktop;

        dispatch(setScreen(isMobile, isTablet, isDesktop, showAsideMenu));
    }

export const checkLang = () =>
    dispatch => {
        let lang = window.navigator ? (window.navigator.language ||
            window.navigator.systemLanguage ||
            window.navigator.userLanguage) : `ru`;
        lang = lang.substr(0, 2).toLowerCase();

        // TODO хардкор русского языка, допилить определение языка на клиенте
        lang = `ru`;

        dispatch(setLang(lang));
    }

export const switchMenu = (show) =>
    dispatch => {
        const showAsideMenu = show;

        dispatch(setShowAsideMenu(showAsideMenu));
    }

export const setScreen = (isMobile, isTablet, isDesktop, showAsideMenu) => {
    return {
        type: APP_SETTINGS_RESIZE_SCREEN,
        isMobile,
        isTablet,
        isDesktop,
        showAsideMenu
    }
}

export const setLang = (lang) => {
    return {
        type: APP_SETTINGS_SET_LANG,
        lang
    }
}

export const setInstallPrompt = (installPrompt) => {
    return {
        type: APP_SETTINGS_SET_INSTALL_PROMPT,
        installPrompt
    }
}

export const setLCDSymbols = (installPrompt) => {
    return {
        type: APP_SETTINGS_SET_LCD_SYMBOLS,
        installPrompt
    }
}

export const setShowAsideMenu = (showAsideMenu) => {
    return {
        type: APP_SETTINGS_SWITCH_ASIDE_MENU,
        showAsideMenu
    }
}
