import React, {Component} from 'react'
import {Switch, Route, Router} from 'react-router-dom'

import history from "./history.js"

import routersCommon from 'routers/routersCommon.js'

export class RouterH extends Component {
    render() {
        return (
            <Router history={history}>
                <Switch>
                    { // common router links
                        routersCommon.map( (it, i) =>
                            (<Route
                                key={`${it.path} ${i}`}
                                path={it.path}
                                component={it.component}
                                exact={it.exact}
                            />)
                        )
                    }
                </Switch>
            </Router>
        );
    }
}


export default RouterH;
